  const navigateinstance = (iid: any) => {
    let x = window.location.protocol
    let a = "//"
    let b = window.location.hostname
    let c = (window.location.hostname === "localhost") ? `:8080/` : (window.location.hostname === "mediacannibal.github.io") ? "/compass2-fe/" : "/"
    return x + a + b + c + "#/" + iid + "/";
  }

  const downloadQRinstance = (id: any) => {
    const canvas = document.getElementById(id);
    const pngUrl = canvas
      .toDataURL("image/png")
      .replace("image/png", "image/octet-stream");
    let downloadLink = document.createElement("a");
    downloadLink.href = pngUrl;
    downloadLink.download = `InstanceQRcode${id}.png`;
    document.body.appendChild(downloadLink);
    downloadLink.click();
    document.body.removeChild(downloadLink);
  };

  //qrcodelevel

